import React from 'react'
import { Form, Button } from 'react-bootstrap'

const hodLogin = () => {
	return (
		<div className = "hod d-flex flex-column justify-content-center align-items-center">
			<h3 className="text-align-center fonts-redressed body-heading">Hod Login</h3>
			<div style={{width: '400px', padding: '50px', backgroundColor: '#efefef'}}>
				<Form className="justify-content-center" style={{width: '300px'}}>
					<Form.Group controlId="formBasicEmail">
						<Form.Label>HoD ID</Form.Label>
						<Form.Control type="email" placeholder="Enter HoD ID" />
						<Form.Text className="text-muted">
								Your Credentials are safe
						</Form.Text>
					</Form.Group>

					<Form.Group controlId="formBasicPassword">
						<Form.Label>Password</Form.Label>
						<Form.Control type="password" placeholder="Password" />
					</Form.Group>
					<Button className="text-align-center" variant="primary" type="submit">
						Log In
					</Button>
					<div className='gap-25'></div>
					<div>New Hod? <a href='/newHoD'>Sign Up</a></div>
					<div>Make sure you keep the Secret Code Ready to sign up as a HoD</div>
				</Form>
			</div>
			<div className="gap-25"></div>
		</div>
    )
}

export default hodLogin


