import React, {useState, useEffect} from 'react'
import { Form, Row, Col, Button } from 'react-bootstrap'

const viewStudent = () => {
    const [name, setName] = useState('--')
    const [age, setAge] = useState('--')
    const [uid, setUID] = useState('--')
    const [sec, setSec] = useState('--')
    const [cgpa, setCGPA] = useState('--')
    const [searchUID, setSearchUID] = useState('')
    const searchStudent = () => {
        // console.log(searchUID);
        let data = 'nothing'
        fetch(`https://protected-forest-70672.herokuapp.com/api/v1/hods/viewStudent/${searchUID}/`)
            .then(response => {
            // console.log(JSON.stringify(response));
            return response.text()
        })
        .then(responseData => {
            data = JSON.parse(responseData)
            console.log(data);
            setName(data.student.name)
            setAge(data.student.age)
            setUID(data.student.uid)
            setSec(data.student.section)
            setCGPA(data.student.cgpa)
        })
        .catch(err => console.log(`Error - ${err}`))
    }
    return (
        <div style={{minHeight: '50vh'}} className="viewteacher d-flex flex-column justify-content-center align-items-center">
            <Form style={{ width: '500px' }}>
                <Form.Group as={Row} controlId="UID">
                    <Form.Label column sm="2" md="4">
                        UID :-
                    </Form.Label>
                    <Col sm="10" md="8">
                        <Form.Control onChange={e => setSearchUID(e.target.value)} plaintext placeholder="UID" />
                    </Col>
                </Form.Group>
                <Button onClick={searchStudent}>
                    Search
                </Button>
            </Form>
            <div className="gap-25"></div>
            <div className="gap-25"></div>
            <div className="justify-content-start">
                <div><b>Name : </b>{name}</div>
                <div><b>Age : </b>{age}</div>
                <div><b>UID : </b>{uid}</div>
                <div><b>Section : </b>{sec}</div>
                <div><b>CGPA : </b>{cgpa}</div>
            </div>
        </div>
    )
}

export default viewStudent


