import React, {useState} from 'react'
import {Form, Row, Col, Button} from 'react-bootstrap'

const createStudent = () => {
    const [name, setName] = useState('')
    const [age, setAge] = useState('--')
    const [uid, setUID] = useState('')
    const [sec, setSec] = useState('--')
    const [cgpa, setCGPA] = useState('--')

    const createStudentReq = () => {
        fetch('https://protected-forest-70672.herokuapp.com/api/v1/hods/addStudent', {
            method: 'POST',
            headers: {
                'Accept': 'application/json',
                'Content-Type': 'application/json'
            },
            body: JSON.stringify({
                name: name,
                age: age,
                uid: uid,
                sec: sec,
                cgpa: cgpa
            })
        })
        .then(response => response.text())
        .then(responseData => {
            const data = JSON.parse(responseData)
        })
    }

    return (
        <div className="createStudent d-flex flex-column justify-content-center align-items-center">
            <h3 className="text-align-center fonts-redressed body-heading">Create Student Record</h3>
            <Form style={{width: '500px'}}>
                <Form.Group as={Row} controlId="name">
                    <Form.Label column sm="2" md="4">
                        Name :-
                    </Form.Label>
                    <Col sm="10"  md="8">
                        <Form.Control value={name} onChange={(e) => setName(e.target.value)} plaintext placeholder="Name" />
                    </Col>
                </Form.Group>

                <Form.Group as={Row} controlId="age">
                    <Form.Label column sm="2" md="4">
                        Age :-
                    </Form.Label>
                    <Col sm="10"  md="8">
                        <Form.Control value={age} onChange={(e) => setAge(e.target.value)} plaintext placeholder="Age" />
                    </Col>
                </Form.Group>

                <Form.Group as={Row} controlId="uid">
                    <Form.Label column sm="2" md="4">
                        UID :-
                    </Form.Label>
                    <Col sm="10"  md="8">
                        <Form.Control value={uid} onChange={(e) => setUID(e.target.value)} plaintext placeholder="UID" />
                    </Col>
                </Form.Group>

                <Form.Group as={Row} controlId="section">
                    <Form.Label column sm="2" md="4">
                        Section :-
                    </Form.Label>
                    <Col sm="10"  md="8">
                        <Form.Control value={sec} onChange={(e) => setSec(e.target.value)} plaintext placeholder="Section" />
                    </Col>
                </Form.Group>

                <Form.Group as={Row} controlId="cgpa">
                    <Form.Label column sm="2" md="4">
                        CGPA :-
                    </Form.Label>
                    <Col sm="10"  md="8">
                        <Form.Control value={cgpa} onChange={(e) => setCGPA(e.target.value)} plaintext placeholder="CGPA" />
                    </Col>
                </Form.Group>

                <Button onClick={createStudentReq} style={{justifySelf: 'center'}} className="text-align-center" variant="primary" type="submit">
                    Create Record
                </Button>
                <div className='gap-25'></div>
            </Form>
        </div>
    )
}

export default createStudent


