import React, {useState, useEffect} from 'react'
import {Row, Col, Button, Form} from 'react-bootstrap'

const viewTeacher = () => {
    const [name, setName] = useState('--')
    const [age, setAge] = useState('--')
    const [uid, setUID] = useState('--')
    const [exp, setExp] = useState('--')
    const [searchUID, setSearchUID] = useState('')
    const searchTeacher = () => {
        let data = 'nothing'
        fetch(`https://protected-forest-70672.herokuapp.com/api/v1/hods/viewTeacher/${searchUID}/`)
            .then(response => {
                // console.log(JSON.stringify(response));
                return response.text()
            })
            .then(responseData => {
                data = JSON.parse(responseData)
                console.log(data);
                setName(data.teacher.name)
                setAge(data.teacher.age)
                setUID(data.teacher.uid)
                setExp(data.teacher.exp)
            })
            .catch(err => console.log(`Error - ${err}`))
    }
    return (
        <div style={{ minHeight: '50vh' }} className="viewteacher d-flex flex-column justify-content-center align-items-center">
            <Form style={{ width: '500px' }}>
                <Form.Group as={Row} controlId="UID">
                    <Form.Label column sm="2" md="4">
                        UID :-
                    </Form.Label>
                    <Col sm="10" md="8">
                        <Form.Control onChange={(e) => setSearchUID(e.target.value)} plaintext placeholder="UID" />
                    </Col>
                </Form.Group>
                <Button onClick={searchTeacher}>
                    Search
                </Button>
            </Form>
            <div className="gap-25"></div>
            <div className="gap-25"></div>
            <div className="justify-content-start">
                <div><b>Name : </b>{name}</div>
                <div><b>Age : </b>{age}</div>
                <div><b>UID : </b>{uid}</div>
                <div><b>Experience : </b>{exp}</div>
            </div>
        </div>
    )
}

export default viewTeacher


