import React, {useState, useEffect} from 'react'
import {Button, Row, Col} from 'react-bootstrap'
import { Redirect } from 'react-router'



const hodPanel = () => {
    const [data, setData] = useState()
    const [isLoading, setIsLoading] = useState(true)
    useEffect(() => {
        if(isLoading==true) {
            fetch('https://run.mocky.io/v3/4f5510e3-10b8-4bf9-be28-fe7a725ef156')
                .then(response => {
                    return response.text()
                })
                .then(responseData => {
                    setData(JSON.parse(responseData))
                    setIsLoading(false)
                })
        }
    }, [data])
    if(isLoading === true) {
        return (
            <div className="hodPanel d-flex flex-column justify-content-center align-items-center" style={{height: '40vh'}}>
                <img className="loader-gif" />
                AUTHENTICATING
            </div>
        )
    }
    else {
        if(data.ifLoggedIn==false) {
            return <Redirect to='/hodlogin' />
        } 
        return (
            <div className='hodPanel d-flex flex-column justify-content-center align-items-center'>
                <h3 className="text-align-center fonts-redressed body-heading">Hod Panel</h3>
                <h5>Welcome HoD</h5>
                <Row>
                    <Col xs={12} md={6} style={{textAlign: 'center'}}>
                        <Button href="/#/createStudent">
                            Create Student Record
                        </Button>
                        <div className="gap-25"></div>
                    </Col>
                    <Col xs={12} md={6} style={{ textAlign: 'center' }}>
                        <Button href="/#/viewStudent">
                            View Student Record
                        </Button>
                        <div className="gap-25"></div>
                    </Col>
                    <div className="gap-25"></div>
                    <Col xs={12} md={6} style={{ textAlign: 'center' }}>
                        <Button href="/#/createteacher">
                            Create Teacher Record
                        </Button>
                        <div className="gap-25"></div>
                    </Col>
                    <Col xs={12} md={6} style={{ textAlign: 'center' }}>
                        <Button href='/#/viewteacher'>
                            View Teacher Record
                        </Button>
                        <div className="gap-25"></div>
                    </Col>
                </Row>
            </div>
        )
    }
}

export default hodPanel


