import React from 'react'
import "react-responsive-carousel/lib/styles/carousel.min.css"
import {Card, Row, Col, Container} from 'react-bootstrap'

const Home = () => {
    return (
        <div className="home overflow-hidden">
            <section id="contact-consultant" data-aos="fade-left" className="custom-jumbotron">
                <div className="elementor-overlay"></div>
                <div className="container overflow-hidden">
                    <div data-aos="zoom-in" className="card jumbo-card">
                        <div className="card-body">
                            <h1 className="card-title fonts-quicksand"><b>Best Private University in the Country</b></h1>
                            <p className="card-text fonts-quicksand" style={{fontSize: "20px"}}> Talk to our Representatives for Admission related Queries. </p>
                            <div className="gap-25"></div>
                            <h4 className="card-title fonts-quicksand text-align-center">FREE CONSULTATION</h4>
                            <div className="gap-25"></div>
                            <div className="text-align-center">
                                <button href="#" className="btn btn-lg btn-success">Call Now <i className="bi bi-telephone-outbound-fill" style={{fontSize: "1rem", color: "#fff"}}></i></button>
                            </div>
                        </div>
                    </div>
                </div>
            </section>

            <h3 id="innovations" className="text-align-center fonts-redressed body-heading">Innovations at the University</h3>
            <Container>
                <Row>
                    <Col xs={12} md={6} lg={4}>
                        <Card style={{ width: '18rem', marginBottom: '20px' }}>
                            <Card.Img variant="top" src="https://www.lpu.in/images/innovation/inno1.jpg" />
                            <Card.Body>
                                <Card.Title>University Students builds Driver less Bus</Card.Title>
                            </Card.Body>
                        </Card>
                    </Col>
                    <Col xs={12} md={6} lg={4}>
                        <Card style={{ width: '18rem', marginBottom: '20px' }}>
                            <Card.Img variant="top" src="https://www.lpu.in/images/innovation/inno6.jpg" />
                            <Card.Body>
                                <Card.Title>CSE Student developed a System to monitor Fridge Hygiene</Card.Title>
                            </Card.Body>
                        </Card>
                    </Col>
                    <Col xs={12} md={6} lg={4}>
                        <Card style={{ width: '18rem', marginBottom: '20px' }}>
                            <Card.Img variant="top" src="https://www.lpu.in/images/innovation/inno5.jpg" />
                            <Card.Body>
                                <Card.Title>DRT choosen among Top 8 Startups in the World</Card.Title>
                            </Card.Body>
                        </Card>
                    </Col>
                    <Col xs={12} md={6} lg={4}>
                        <Card style={{ width: '18rem', marginBottom: '20px' }}>
                            <Card.Img variant="top" src="https://www.lpu.in/images/innovation/inno7.jpg" />
                            <Card.Body>
                                <Card.Title>E-Parikshan by LPU Students for Smart Farming Featured in India Today</Card.Title>
                            </Card.Body>
                        </Card>
                    </Col>
                    <Col xs={12} md={6} lg={4}>
                        <Card style={{ width: '18rem', marginBottom: '20px' }}>
                            <Card.Img variant="top" src="https://www.lpu.in/images/innovation/inno13.jpg" />
                            <Card.Body>
                                <Card.Title>University gets Patent for 'Smart' Drug Delivery System</Card.Title>
                            </Card.Body>
                        </Card>
                    </Col>
                    <Col xs={12} md={6} lg={4}>
                        <Card style={{ width: '18rem', marginBottom: '20px' }}>
                            <Card.Img variant="top" src="https://www.lpu.in/images/innovation/inno18.jpg" />
                            <Card.Body>
                                <Card.Title>Student won STEM Engagement Award 2019 by NASA</Card.Title>
                            </Card.Body>
                        </Card>
                    </Col>
                </Row>
            </Container>

            <h3 id="latest-news" className="text-align-center fonts-redressed body-heading">LATEST NEWS!</h3>
            <Container>
                <Row>
                    <Col xs={12} md={6} lg={4}>
                        <Card style={{ width: '18rem', marginBottom: '20px' }}>
                            <Card.Img variant="top" src="https://www.lpu.in/images/google.jpg" />
                            <Card.Body>
                                <Card.Title>Collaboration with Google: Signs up MoU for Hosting Google Center of Excellence At Campus</Card.Title>
                            </Card.Body>
                        </Card>
                    </Col>
                    <Col xs={12} md={6} lg={4}>
                        <Card style={{ width: '18rem', marginBottom: '20px' }}>
                            <Card.Img variant="top" src="https://www.lpu.in/images/vmware.jpg" />
                            <Card.Body>
                                <Card.Title>University Joins Hands With VMware IT Academy</Card.Title>
                            </Card.Body>
                        </Card>
                    </Col>
                    <Col xs={12} md={6} lg={4}>
                        <Card style={{ width: '18rem', marginBottom: '20px' }}>
                            <Card.Img variant="top" src="https://www.lpu.in/images/collage1.jpg" />
                            <Card.Body>
                                <Card.Title>University Has Introduced Specialized B.Tech. Computer Science And Engineering Programmes In Tie-Up With Top Industry Brands</Card.Title>
                            </Card.Body>
                        </Card>
                    </Col>
                    <Col xs={12} md={6} lg={4}>
                        <Card style={{ width: '18rem', marginBottom: '20px' }}>
                            <Card.Img variant="top" src="https://www.lpu.in/images/collage2.jpg" />
                            <Card.Body>
                                <Card.Title>University In Association With Top Industry Leaders Has Introduced Industry Driven 2-Year Specialized MBA Programmes</Card.Title>
                            </Card.Body>
                        </Card>
                    </Col>
                    <Col xs={12} md={6} lg={4}>
                        <Card style={{ width: '18rem', marginBottom: '20px' }}>
                            <Card.Img variant="top" src="https://www.lpu.in/images/tanya-arora.jpg" />
                            <Card.Body>
                                <Card.Title>University Student Tanya Arora Hired by Microsoft for a package of 42 Lacs INR</Card.Title>
                            </Card.Body>
                        </Card>
                    </Col>
                    <Col xs={12} md={6} lg={4}>
                        <Card style={{ width: '18rem', marginBottom: '20px' }}>
                            <Card.Img variant="top" src="https://www.lpu.in/images/wayne.jpg" />
                            <Card.Body>
                                <Card.Title>Full Scholarship Worth Over $100,000 For LPU Student To Pursue PhD In The Wayne State University, US</Card.Title>
                            </Card.Body>
                        </Card>
                    </Col>
                </Row>
            </Container>
        </div>
    )
}

export default Home


