import React from 'react'
import { Row, Col, Container, Card } from 'react-bootstrap'

const Placements = (props) => {
    return (
        <div className="placements justify-content-center">
            <h3 id="placements" className="text-align-center fonts-redressed body-heading">Placements</h3>
            <Container className="justify-content-center">
                <Row>
                    <Col xs={12} md={6} lg={4}>
                        <Card style={{ width: '18rem', marginBottom: '20px' }}>
                            <Card.Img variant="top" src="https://www.lpu.in/images/placement-new/Amazon-offers-3-students-a-record-Rs-28-lakh-package-28-students-offered-internships.jpg" />
                            <Card.Body>
                                <Card.Title>Amazon Offers LPU Students a record Rs. 30 Lac Package and Internship</Card.Title>
                            </Card.Body>
                        </Card>
                    </Col>
                    <Col xs={12} md={6} lg={4}>
                        <Card style={{ width: '18rem', marginBottom: '20px' }}>
                            <Card.Img variant="top" src="https://www.lpu.in/images/tanya-arora.jpg" />
                            <Card.Body>
                                <Card.Title>LPU Student Tanya Arora Hired by Microsoft at a package of Rs. 42 Lac</Card.Title>
                            </Card.Body>
                        </Card>
                    </Col>
                    <Col xs={12} md={6} lg={4}>
                        <Card style={{ width: '18rem', marginBottom: '20px' }}>
                            <Card.Img variant="top" src="https://www.lpu.in/images/placement-new/LPU-alumni-Utkarsh-placed-at-Google-HQs-in-Silicon-Valley.jpg" />
                            <Card.Body>
                                <Card.Title>University Alumini, Utkarsh, Placed at Google HQs in Silicon Valley at 1 Cr.+ Package</Card.Title>
                            </Card.Body>
                        </Card>
                    </Col>
                    <Col xs={12} md={6} lg={4}>
                        <Card style={{ width: '18rem', marginBottom: '20px' }}>
                            <Card.Img variant="top" src="https://www.lpu.in/images/placement-new/Neha-after-having-worked-at-Samsung-and-Yahoo-now-works-at-Facebook-HQs.jpg" />
                            <Card.Body>
                                <Card.Title>Neha, after having worked at Samsung and Yahoo, now works at Facebook HQs</Card.Title>
                            </Card.Body>
                        </Card>
                    </Col>
                    <Col xs={12} md={6} lg={4}>
                        <Card style={{ width: '18rem', marginBottom: '20px' }}>
                            <Card.Img variant="top" src="https://www.lpu.in/images/placement-new/Over-10000-offers-made-in-recent-years-by-bulk-recruiters.jpg" />
                            <Card.Body>
                                <Card.Title>Over 10,000 offers made in recent years by Top Recruiters.</Card.Title>
                            </Card.Body>
                        </Card>
                    </Col>
                    <Col xs={12} md={6} lg={4}>
                        <Card style={{ width: '18rem', marginBottom: '20px' }}>
                            <Card.Img variant="top" src="https://www.lpu.in/images/placement-new/cognizant.jpg" />
                            <Card.Body>
                                <Card.Title>Cognizant hired 3300+ LPU students in recent years</Card.Title>
                            </Card.Body>
                        </Card>
                    </Col>
                </Row>
            </Container>
        </div>
    )
}

export default Placements


