import 'bootstrap/dist/css/bootstrap.min.css';
import './App.css';
import React, {useState, useEffect} from 'react';
import { HashRouter as Router, Switch, Route } from 'react-router-dom';

import NavbarCustom from './Navbar.js'
import Home from './Screens/Home.js'
import Placements from './Screens/Placements'
import Courses from './Screens/Courses'
import Footer from './Footer.js'
import hodLogin from './Screens/hodLogin'
import hodPanel from './Screens/hodPanel'
import createStudent from './Screens/createStudent'
import createTeacher from './Screens/createTeacher'
import viewStudent from './Screens/viewStudent'
import viewTeacher from './Screens/viewTeacher'

function App() {
  const [currentPage, setCurrentPage] = useState()
  useEffect(() => {
    setCurrentPage('/')
  }, [])
  return (
    <div className='App'>
      <NavbarCustom currentPage={currentPage} setCurrentPage={setCurrentPage} />
      <Router>
        <Switch>
          <Route path={`/`} exact component={Home} />
          <Route path={`/placements`} exact component={Placements} />
          <Route path={`/courses`} exact component={Courses} />
          <Route path={`/hodLogin`} exact component={hodLogin} />
          <Route path={`/hodPanel`} exact component={hodPanel} />
          <Route path={`/createStudent`} exact component={createStudent} />
          <Route path={`/createTeacher`} exact component={createTeacher} />
          <Route path={`/viewStudent`} exact component={viewStudent} />
          <Route path={`/viewTeacher`} exact component={viewTeacher} />
        </Switch>
      </Router>
      <Footer />
    </div>
  );
}

export default App;



